import React, {useRef, Suspense} from "react";
import { Switch, Route, Redirect } from "react-router-dom";
// creates a beautiful scrollbar
import PerfectScrollbar from "perfect-scrollbar";
import "perfect-scrollbar/css/perfect-scrollbar.css";
// @material-ui/core components
import { makeStyles } from "@material-ui/core/styles";
import CircularProgress from '@material-ui/core/CircularProgress';
// core components
import Navbar from "components/Navbars/Navbar.js";
import Footer from "components/Footer/Footer.js";
import Sidebar from "components/Sidebar/Sidebar.js";
import FixedPlugin from "components/FixedPlugin/FixedPlugin.js";

import routes from "routes.js";

import styles from "assets/jss/material-dashboard-react/layouts/adminStyle.js";

import bgImage from "assets/img/sidebar-4.jpg";
import logo from "assets/img/reactlogo.png";
import { useEffect } from "react";

import audio from 'assets/audio/pop.ogg'

let ps;

const SwitchRoutes = ({addCallBack, deleteCallBack, webSocket}) => (
  <Suspense fallback={<LinearBuffer/>}>
  <Switch>
    {routes.map((prop, key) => {
      if (prop.layout === "/lax_medic") {
        return (
          <Route
            path={prop.layout + prop.path}
            key={key}
          >
            <prop.component addCallBack={addCallBack} deleteCallBack={deleteCallBack} webSocket={webSocket}/>
          </Route>
        );
      }
      return null;
    })}
    <Redirect from="/lax_medic" to="/lax_medic/acceuil" />
  </Switch>
      </Suspense>
);

function LinearBuffer() {
  const useStyles = makeStyles({
  root: {
    left: '0',
    height: 'calc(100vh - 123px)',
    width: '100%',
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
    zIndex: 99
  },
});
  const classes = useStyles();
  return (
    <div className={classes.root}>
      <CircularProgress />
    </div>
  );
}

const useStyles = makeStyles(styles);

let callBackList = [];

export default function Main({webSocket, ...rest }) {
  const addCallBack = (callBack) => {
    console.log('add callBack fun !!!!!')
    callBackList.push(callBack);
  }

  const deleteCallBack = (callBackId) => {
    console.log('delete callBack func'+callBackId+' !!!!!')
    callBackList = callBackList.filter(callBack => callBack.id !== callBackId);
    console.log(callBackList)
  }

  const audioRef = useRef(null)
  React.useEffect(() => {

    addCallBack({
      id: 'NotificationSound',
      type: [
        'chat'
      ],

      run: (data) => {
        console.log('je suis là depuis ReceptionHome')
        console.log(data)
        audioRef.current.play();
      }
    })
    return () => {
      deleteCallBack('NotificationSound')
    }
  }, [])

  useEffect(() => {
    webSocket.onmessage = (e)=>{
      const data = JSON.parse(e.data)
      console.log(data)
      console.log(callBackList)
      callBackList.forEach((callBack)=>{
        if(callBack.type.includes(data.type)){
          callBack.run(data)
        }
      })
    }
  }, [webSocket.onmessage]);
  // styles
  const classes = useStyles();
  // ref to help us initialize PerfectScrollbar on windows devices
  const mainPanel = React.createRef();
  // states and functions
  const [image, setImage] = React.useState(bgImage);
  const [color, setColor] = React.useState("blue");
  const [fixedClasses, setFixedClasses] = React.useState("dropdown");
  const [mobileOpen, setMobileOpen] = React.useState(false);
  const handleImageClick = image => {
    setImage(image);
  };
  const handleColorClick = color => {
    setColor(color);
  };
  const handleFixedClick = () => {
    if (fixedClasses === "dropdown") {
      setFixedClasses("dropdown show");
    } else {
      setFixedClasses("dropdown");
    }
  };
  const handleDrawerToggle = () => {
    console.log('modile = ' + mobileOpen)
    setMobileOpen(value => !value);
  };
  const resizeFunction = () => {
    if (window.innerWidth >= 960) {
      setMobileOpen(false);
    }
  };
  // initialize and destroy the PerfectScrollbar plugin
  React.useEffect(() => {
    if (navigator.platform.indexOf("Win") > -1) {
      ps = new PerfectScrollbar(mainPanel.current, {
        suppressScrollX: true,
        suppressScrollY: false
      });
      document.body.style.overflow = "hidden";
    }
    window.addEventListener("resize", resizeFunction);
    // Specify how to clean up after this effect:
    return function cleanup() {
      if (navigator.platform.indexOf("Win") > -1) {
        ps.destroy();
      }
      window.removeEventListener("resize", resizeFunction);
    };
  }, [mainPanel]);
  return (
    <div className={classes.wrapper}>
      {
    console.log('render main')}
      <Sidebar
        routes={routes}
        logoText={"lax_medic"}
        logo={logo}
        image={image}
        handleDrawerToggle={handleDrawerToggle}
        open={mobileOpen}
        color={color}
        {...rest}
      />
      <div className={classes.mainPanel} ref={mainPanel}>
        <Navbar
          routes={routes}
          handleDrawerToggle={handleDrawerToggle}
          color={'white'}
          {...rest}
        />
        {/* On the /maps route we want the map to be on full screen - this is not possible if the content and conatiner classes are present because they have some paddings which would make the map smaller */}

          <div className={classes.content}>
            <div className={classes.container}>
              <SwitchRoutes addCallBack={addCallBack} deleteCallBack={deleteCallBack} webSocket={webSocket}/>
            </div>
          </div>

        <Footer />{/* 
        <FixedPlugin
          handleImageClick={handleImageClick}
          handleColorClick={handleColorClick}
          bgColor={color}
          bgImage={image}
          handleFixedClick={handleFixedClick}
          fixedClasses={fixedClasses}
        /> */}
      </div>
      <audio ref={audioRef}>
    <source src={audio}></source>{/* 
    <source src="/static/audio/playerScore.mp3"></source> */}
    </audio>
    </div>
  );
}
